<template>
  <div id="editPreview">
    <header-component />
    <div id="loadingPreview" v-loading="loading">
      <div id="editpagePreview" v-show="onload">
        <div
          id="questionnairePreview"
          ref="previewHeight"
          @scroll="scrollDefault"
        >
          <div id="topBox" :class="[scrollable ? 'queBox-remain' : 'queBox']">
            预览
          </div>
          <div
            id="queBoxPreview"
            :class="[scrollable ? 'queBox-remain' : 'queBox']"
          >
            <div id="headPreview">
              <div id="namePreview">
                <span>{{ queName }}</span>
              </div>
              <div id="interpretationPreview">
                <span>
                  {{ queIntp }}
                </span>
              </div>
            </div>
            <div id="modelsPreview" ref="models">
              <div
                v-for="que in quesFiltered"
                :key="que.id"
                class="list-complete-item"
              >
                <div class="que-title-holder">
                  <div class="redStarHolder">
                    <img
                      v-if="que.compulsory"
                      src="../assets/pictures/redStar.svg"
                      alt="*"
                      width="7px"
                      height="7px"
                    />
                    <div v-else style="width: 7px;height: 7px;"></div>
                  </div>
                  <div class="queIndex">
                    <div>{{ queUltimateIndex(que.id) + 1 }}.</div>
                  </div>
                  <span
                    :class="
                      que.titleImgSrc != '' ? 'lengthened' : 'span-origin'
                    "
                    >{{ que.title }}</span
                  >
                  <div class="pic-holder">
                    <div class="label-file label-title">
                      <el-image
                        v-if="que.titleImgSrc != ''"
                        :src="que.titleImgSrc"
                        :preview-src-list="[que.titleImgSrc]"
                        fit="cover"
                        style="width: 135px;height: 90px;border-radius: 5px;"
                      ></el-image>
                    </div>
                  </div>
                </div>

                <div v-if="que.type == '单选题'">
                  <div
                    v-for="choice in que.radio.choices"
                    :key="choice.id"
                    class="choices-item"
                  >
                    <li>
                      <div>
                        <input
                          type="radio"
                          v-model="que.radio.value"
                          :value="
                            choice.blank_fill === ''
                              ? choice.content
                              : choice.content + '|ST23|' + choice.blank_fill
                          "
                          class="choiceBox"
                          :id="`${que.id}` + choice.fileId"
                          :checked="choice.checked"
                        />
                      </div>
                      <label :for="`${que.id}` + choice.fileId">
                        {{ choice.content }}
                      </label>
                    </li>
                    <div
                      class="bfa-input"
                      v-if="
                        chosen(que.radio.value, choice.content) &&
                          choice.needFilling
                      "
                      :style="choice.imgSrc ? 'margin-bottom: 5px;' : ''"
                    >
                      <div class="redStarHolder">
                        <img
                          v-if="choice.compulsory"
                          src="../assets/pictures/redStar.svg"
                          alt="*"
                          width="7px"
                          height="7px"
                        />
                        <div v-else style="width: 7px;height: 0px;"></div>
                      </div>
                      <input type="text" v-model="choice.blank_fill" v-focus />
                    </div>
                    <div class="pic-preview">
                      <el-image
                        v-if="choice.imgSrc != ''"
                        :src="choice.imgSrc"
                        :preview-src-list="[choice.imgSrc]"
                        fit="cover"
                        style="width: 135px;height: 90px;border-radius: 5px;"
                      ></el-image>
                    </div>
                  </div>
                </div>

                <div v-else-if="que.type == '多选题'">
                  <div
                    v-for="option in que.multiple.options"
                    :key="option.id"
                    class="options-item"
                  >
                    <li>
                      <div>
                        <input
                          type="checkbox"
                          v-model="que.multiple.value"
                          :value="
                            option.blank_fill === ''
                              ? option.content
                              : option.content + '|ST23|' + option.blank_fill
                          "
                          class="choiceBox"
                          :id="`${que.id}` + option.fileId"
                        />
                      </div>
                      <label :for="`${que.id}` + option.fileId">{{
                        option.content
                      }}</label>
                    </li>
                    <div
                      class="bfa-input"
                      v-if="
                        chosen(que.multiple.value, option.content) &&
                          option.needFilling
                      "
                      :style="option.imgSrc ? 'margin-bottom: 5px;' : ''"
                    >
                      <div class="redStarHolder">
                        <img
                          v-if="option.compulsory"
                          src="../assets/pictures/redStar.svg"
                          alt="*"
                          width="7px"
                          height="7px"
                        />
                        <div v-else style="width: 7px;height: 0px;"></div>
                      </div>
                      <input type="text" v-model="option.blank_fill" v-focus />
                    </div>
                    <div class="pic-preview">
                      <el-image
                        v-if="option.imgSrc != ''"
                        :src="option.imgSrc"
                        :preview-src-list="[option.imgSrc]"
                        fit="cover"
                        style="width: 135px;height: 90px;border-radius: 5px;"
                      ></el-image>
                    </div>
                  </div>
                </div>

                <div v-else-if="que.type == '填空题'">
                  <div>
                    <textarea
                      type="text"
                      v-model="que.blank_fill"
                      class="blank"
                      placeholder="文本框"
                    ></textarea>
                  </div>
                </div>
                <div v-else-if="que.type == '排序题'">
                  <div
                    v-for="sortItem in que.sort.sortItems"
                    :key="sortItem.id"
                    class="sortItems-item"
                  >
                    <li>
                      <div>
                        <div
                          :class="[
                            'orderBox',
                            sortItem.order ? 'orderBox-active' : '',
                          ]"
                          @click="sortHandler(que.sort, sortItem)"
                        >
                          {{ sortItem.order ? sortItem.order : "" }}
                        </div>
                      </div>
                      <span>
                        {{ sortItem.content }}
                      </span>
                    </li>
                    <div class="pic-preview">
                      <el-image
                        v-if="sortItem.imgSrc != ''"
                        :src="sortItem.imgSrc"
                        :preview-src-list="[sortItem.imgSrc]"
                        fit="cover"
                        style="width: 135px;height: 90px;border-radius: 5px;"
                      ></el-image>
                    </div>
                  </div>
                </div>
                <div v-else-if="que.type == '评分题'">
                  <div>
                    <div class="slider">
                      <el-slider
                        v-model="que.slider.value"
                        :marks="que.slider.marks"
                        :min="que.slider.min"
                        :max="que.slider.max"
                        :step="
                          que.slider.max - que.slider.min > 10 ? 0.1 : 0.01
                        "
                        @change="que.slider.showToolTip = true"
                        :show-tooltip="que.slider.showToolTip"
                      >
                      </el-slider>
                    </div>
                  </div>
                </div>
                <div v-else-if="que.type == '矩阵单选题'">
                  <div class="matrix-holder">
                    <div class="header-preview">
                      <div class="empty"></div>
                      <div class="columnTitles">
                        <div
                          v-for="(column, index) in que.matrixRadio.columnVals"
                          :key="index"
                        >
                          <span>{{ column.content }}</span>
                        </div>
                      </div>
                    </div>
                    <div
                      v-for="(row, index) in que.matrixRadio.rowVals"
                      :key="index"
                      class="row-item"
                    >
                      <div class="row-item-title">{{ row.content }}</div>
                      <div class="row-item-choices">
                        <div
                          v-for="(column, index) in que.matrixRadio.columnVals"
                          :key="index"
                          class="row-item-choice"
                        >
                          <div>
                            <input
                              type="radio"
                              v-model="row.value"
                              :value="column.content"
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div v-else>
                  <div class="matrix-holder">
                    <div class="header-preview">
                      <div class="empty"></div>
                      <div class="columnTitles">
                        <div
                          v-for="(column, index) in que.matrixMultiple
                            .columnVals"
                          :key="index"
                        >
                          <span>{{ column.content }}</span>
                        </div>
                      </div>
                    </div>
                    <div
                      v-for="(row, index) in que.matrixMultiple.rowVals"
                      :key="index"
                      class="row-item"
                    >
                      <div class="row-item-title">{{ row.content }}</div>
                      <div class="row-item-choices">
                        <div
                          v-for="column in que.matrixMultiple.columnVals"
                          :key="column.id"
                          class="row-item-choice"
                        >
                          <div>
                            <input
                              type="checkbox"
                              v-model="row.value"
                              :value="column.content"
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div id="page">
              <button
                type="button"
                @click="currentPage--"
                :disabled="currentPage === 1"
                :class="[currentPage === 1 ? 'disabled' : 'avaliable']"
              >
                上一页
              </button>
              {{ currentPage }}/{{ totalPage }}页
              <button
                type="button"
                @click="currentPage++"
                :disabled="currentPage === totalPage"
                :class="[currentPage === totalPage ? 'disabled' : 'avaliable']"
              >
                下一页
              </button>
            </div>
          </div>
        </div>
        <div id="generalButtsPreview">
          <div>
            <img
              src="../assets/pictures/settings.svg"
              width="25px"
              height="25px"
            /><span>设置</span>
          </div>
          <div>
            <img
              src="../assets/pictures/save.svg"
              width="23px"
              height="23px"
            /><span>保存</span>
          </div>
          <div>
            <img
              src="../assets/pictures/publish.svg"
              width="21px"
              height="21px"
            /><span>发布</span>
          </div>
          <div @click="toEdit">
            <img
              src="../assets/pictures/back.svg"
              width="18px"
              height="18px"
            /><span>返回</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComponent from "../components/HeaderComponent.vue";
import resizeDetector from "element-resize-detector";
import { mapState } from "vuex";
export default {
  name: "Preview",
  components: {
    HeaderComponent,
  },
  data() {
    return {
      onload: false,
      loading: true,
      currentPage: 1,
      scrollable: false,
      ques: [],
    };
  },
  directives: {
    focus: {
      inserted: function(el) {
        el.focus();
      },
    },
  },
  mounted() {
    const rd = resizeDetector();
    rd.listenTo(this.$refs.previewHeight, (element) => {
      if (element.scrollHeight > element.clientHeight) this.scrollable = true;
      else this.scrollable = false;
    });
    this.ques = this.getQues();
    setTimeout(() => {
      this.onload = true;
      this.loading = false;
    }, 500);
  },
  computed: {
    ...mapState(["questionnaireObj"]),
    queName() {
      return this.questionnaireObj.name;
    },
    queIntp() {
      return this.questionnaireObj.interpretation;
    },
    totalPage() {
      return this.questionnaireObj.totalPage;
    },
    quesReceived() {
      return this.questionnaireObj.data;
    },
    quesUltimate() {
      return this.ques.filter((que) => que.type !== "pagingLine");
    },
    quesFiltered() {
      return this.quesUltimate.filter((que) => que.page === this.currentPage);
    },
  },
  methods: {
    getQues() {
      if (this.quesReceived == false) {
        this.$message.warning("无可预览内容！");
        this.$router.push({ name: "HomePage" });
        return [];
      } else {
        let received = JSON.parse(JSON.stringify(this.quesReceived));
        let dispatchedNum = 1;
        received.map((que) => {
          que.page = dispatchedNum;
          if (que.type === "pagingLine") dispatchedNum++;
          if (que.random) {
            if (que.type === "单选题") this.randomize(que.radio.choices);
            else if (que.type === "多选题")
              this.randomize(que.multiple.options);
            else if (que.type === "排序题") this.randomize(que.sort.sortItems);
            else if (que.type === "矩阵单选题")
              this.randomize(que.matrixRadio.columnVals);
            else if (que.type === "矩阵多选题")
              this.randomize(que.matrixMultiple.columnVals);
          }
        });
        return received;
      }
    },
    queUltimateIndex(id) {
      let i = -1;
      this.quesUltimate.map((que, index) => {
        if (que.id === id) i = index;
      });
      return i;
    },
    chosen(a, b) {
      if (typeof a === "string") return a.split("|ST23|")[0] === b;
      else {
        let flag;
        a.map((item) => {
          if (item.split("|ST23|")[0] === b) flag = true;
        });
        return flag;
      }
    },
    randomize(arr) {
      arr.sort(() => {
        return Math.random() - 0.5;
      });
    },
    sortHandler(sort, sortItem) {
      if (!sort.sortedNum) sortItem.order = ++sort.sortedNum;
      else if (!sortItem.order) sortItem.order = ++sort.sortedNum;
      else if (sortItem.order == sort.sortedNum) {
        sortItem.order = 0;
        sort.sortedNum--;
      } else {
        let tempOrder = sortItem.order;
        sort.sortItems.map((item) => {
          if (item.order > tempOrder) item.order--;
        });
        sortItem.order = 0;
        sort.sortedNum--;
      }
    },
    scrollDefault() {
      this.ques.map((que) => {
        if (que.type === "评分题") que.slider.showToolTip = false;
      });
    },
    toEdit() {
      this.$router.push({ name: "EditPage" });
    },
  },
};
</script>

